<template>
  
          <b-card-group deck>
            <div  v-for="proyecto in proyectos" :key="proyecto.id">
              
            <Actuals :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto" :url="proyecto.url"></Actuals> 
            </div>
            
           </b-card-group>

</template>

<script>
import Actuals from '../components/Actuals.vue' 
const lstProysJson = '../llistaexpo.json';

export default {
  props: {  
   name: {
      type: String,
      default: 'expo'
    }
  },
  components: {
        Actuals
      },
  data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },

    methods: {
        async fetchData() {
          const res = await fetch (lstProysJson);
          const val= await res.json();
          this.proyectos=val;
          console.log(val);
        }
    }
  }
</script>


<style>

.card-deck{
  justify-content: center;
  margin-bottom:10px ;
}


.card-img, .card-img-top {
  
     border-top-left-radius: 0; 
     border-top-right-radius: 0;    
     filter: saturate(0%);
    width: 250px;
    object-fit: fill;
    overflow: hidden;

}
nav{
  height: auto;
}

.b-card-img,.b-card-img-top:hover {
    transform: scale(1.1);
    opacity: 1;
    cursor: crosshair;
    transition: .5s ease;
    filter: contrast(120%) brightness(100%) saturate(100%);
}
</style>